@font-face {
    font-family: "Helvetica";
    src: local("Helvetica-Regular"), url("./fonts/helvetica_regular.woff2") format("woff2"),
        url("./fonts/helvetica_regular.woff") format("woff");
    font-display: swap;
    font-weight: 400;
    font-style: normal;
}

body {
    margin: 0;
    box-sizing: border-box;
    font-family: "Helvetica", sans-serif;
    font-style: normal;
    font-weight: normal;
    background-color: #e5e5e5;
}
